.BuildController {
   display: flex;
   justify-content: space-between;
   align-items: center;
   margin: 5px 0;
}

.BuildController button {
   display: block;
   font: inherit;
   padding: 5px;
   margin: 0px 5px;
   width: 80px;
   cursor: pointer;
   outline: none;
   border: 1px solid #aa6817;
}

.BuildController button:disabled {
   background-color: #ac9980;
   border: 1px solid #7e7365;
   color: #ccc;
   cursor: default;
}

.BuildController button:hover:disabled {
   background-color: #ac9980;
   color: #ccc;
   cursor: not-allowed;
}

.Label {
   padding: 10px;
   font-weight: bold;
   width: 80px;
}

.BuildController .Less {
    background-color: #d39952;
    color: white;
}

.BuildController .More{
    background-color: #8f5e1e;
    color: white;
}

.BuildController .Less:hover, 
.BuildController .Less:active{
    background-color: #daa972;
    color: white;
}

.BuildController .More:hover,
.BuildController .More:active{
    background-color: #99703f;
    color: white;
}
